export default {
	data() {
		return {
			height: '',
			opens: -1,
			merchantId: '',
			orders: [],
			page: 1,
		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
				that.refundOrder();
			}
		});
	},
	onShow() {
		this.page=1;
		this.refundOrder();
	},
	// 上拉加载
	onReachBottom() {
		this.page++;
		this.refundOrder();
	},
	onReady() {
		this.getTopheight();
	},
	methods: {
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
				}
			})
		},
		wz: function(latitude, longitude) {
			uni.openLocation({
				latitude: latitude,
				longitude: longitude,
				success: function() {}
			});
		},
		dh: function(phone) {
			uni.makePhoneCall({
				phoneNumber: phone,
				success: function(res) {}
			});
		},
		open: function(index) {
			this.tg = !this.tg;
			if (this.opens != index) {
				this.opens = index;
			} else {
				this.opens = -1;
			}
		},
		refundOrder: function() {
			let that = this;
			let neworder = [];
			uni.showLoading({
				
			})
			uni.request({
				url: this.serveipd + '/api/merchant/order/refundOrder',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
					page: that.page,
				},
				success: res => {
					uni.hideLoading();
					neworder = res.data.data;
					if (neworder.length == 0) {
						uni.showToast({
							title: '没有更多数据',
							icon: 'none'
						});
						return
					}
					that.orders = that.orders.concat(neworder);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		see_infor: function(orderno) {
			let that = this;
			uni.navigateTo({
				url: "./refund_details?orderno=" + orderno,
			})
		},
	}
}
